import React, { Component } from 'react';
import '../../App.css';

class Upcoming extends Component {
	render() {
		return (
			<div id='Upcoming'>
				<h2 className='tc'> 
					<span className='title'>
					UPCOMING PROJECTS.
					</span> 
				</h2>
				<div className="container3">
					<div className='' id='project1'>
						<p className='item-a portfolioParagraph i'>
						StillAlive <br />
						<span className='portfolioParagraph2'>Web App<span className='light-red'>.</span></span>
						</p>
						<p className='item-b portfolioParagraph3'>
						Web and Mobile App that wants to help 
						make visible endangered species. <br />
						</p>
					</div>
					<div className='' id='project2'>
						<p className='item-a portfolioParagraph i'>
						MD<span className='light-red'>*</span> Workers <br />
						<span className='portfolioParagraph2'>Publication<span className='light-red'>.</span></span>
						</p>
						<p className='item-b portfolioParagraph3'>
						An investigation about the effects of multi-
						disciplinary (MD) workers in different environments
						such as arts and technology. <br />
						</p>
					</div>
					<div className='' id='project3'>
						<p className='item-a portfolioParagraph i'>
						LatinArt <br />
						<span className='portfolioParagraph2'>Mobile App<span className='light-red'>.</span></span>
						</p>
						<p className='item-b portfolioParagraph3'>
						Mobile App that intends to promote and give more
						visibility to Latin American Artists, sharing their work
						and biography. <br />
						</p>
					</div>
				</div>
			</div>
		)
	}
}

export default Upcoming;